@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
@media (max-width: 991px) {
  .lista-cases {
    grid-template-columns: repeat(2, 1fr) !important;
  }
}
@media (max-width: 767px) {
  .lista-cases {
    grid-template-columns: repeat(1, 1fr) !important;
  }
}
@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
  }
  .fields {
    flex-direction: column;
  }
  .submit-button {
    text-align: center;
  }
  .section-title {
    padding-left: 25px;
  }
  .section-title::before {
    left: 0 !important;
  }
  .black-mask-mob {
    position: relative;
    z-index: 0;
  }
  .black-mask-mob::before, .black-mask-mob::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.54);
    z-index: -1;
  }
}
@media (max-width: 767px) {
  .modal .modal-content .modal-body .items-slider .slider-item .texts {
    bottom: initial !important;
  }
}
@media (max-width: 991px) {
  header #menu-mobile {
    display: block;
  }
}
@media (max-width: 768px) {
  #btn-header-orcamento {
    display: none;
  }
  header .container #div-search {
    left: 0;
    padding: 0 15px;
  }
  header .container .menu-header #secondary-menu-header {
    display: none;
  }
  header .container #menu-mobile {
    position: absolute;
    top: 0;
    left: 0;
    background: #cae098;
    width: 100dvw;
    height: 100dvh;
    padding: 15px 30px;
    overflow: auto;
    transform: translateX(-1200px);
    transition: all ease 0.5s;
    z-index: 1000;
  }
  header .container #menu-mobile #menu-menu-principal-1 {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  header .container #menu-mobile #menu-menu-principal-1 li a {
    font-size: 16px;
    color: #006635;
    font-weight: bold;
  }
  header .container #menu-mobile #menu-menu-principal-1 li ul {
    display: block;
    position: relative;
    background: transparent;
    padding: 5px 0;
  }
  header .container #menu-mobile #menu-menu-principal-1 li ul li {
    padding: 0;
  }
  header .container #menu-mobile #menu-menu-principal-1 li ul.sub-menu a {
    font-weight: lighter;
  }
  header .container #menu-mobile #menu-menu-principal-1 li::after {
    color: #cae098;
  }
  header .container #menu-mobile #close-menu-mobile {
    position: fixed;
    top: 15px;
    right: 20px;
    z-index: 101;
  }
  header .container #menu-mobile #close-menu-mobile svg {
    width: 25px;
  }
  header .container #menu-mobile #close-menu-mobile svg path {
    fill: #006635;
  }
  header .container #menu-mobile.active {
    transform: translateX(0);
  }
}
@media (min-width: 575px) {
  header #menu-mobile.active {
    left: -24px;
  }
}
@media (max-width: 768px) {
  #slider-topo .container .slider-text {
